* {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: black;
}
h2{ font-size: 1.6em; padding-left: 3em;}
h3{ font-size: 1em;padding-left: 5em;}
html,body{
    background-color: #fff;
    padding: 1%;
}
.app{
    background: rgba(0, 0, 0, 0.1);
    padding: 2%;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
}
.stats{
    flex:1;
    padding:1em;
    border-top-right-radius: 30px;
    border-top-left-radius: 30px;
    align-content: flex-end;
    font-style: italic;
}
.stats li {display: inline-flex; padding-right: 1em;}
.stats li i {font-weight: bolder; padding-left: 1em; font-size: 1.2em; font-style: normal; min-width: 3em;}
.column-container {
    flex:10;
    display: flex;
    flex-direction: row;
}
.column{
    flex: 1;
}
.column:nth-child(2){
    flex: 2;
}
.list{
    display: flex;
    flex-direction: column;
}
.list-item{
    list-style-type: none;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 16px;
    padding: 1em;
    margin: 1em;
    text-align: center;
    display: flex;
}
.list-item span{
    flex: 10;
}
.column:nth-child(1) .list-item{
color: rgba(0, 0, 0, 0.5);
font-size: 0.9em;
}
.list-item i{flex: 1;
justify-content:right;
align-content: flex-end;

}
@media (prefers-color-scheme: dark) {
* {
color: aliceblue;
}
    html,body{
        background-color: #1e2441;
    }    

    .app{
        background: rgba(255, 255, 255, 0.1);
    }    
    .list-item{
        border: 1px solid rgba(255, 255, 255, 0.2);
    }    
    .column:nth-child(1) .list-item{
        color: rgba(255, 255, 255, 0.5);
    }        
}
